html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #40C2D6;
}

.py-2 {
  padding: 2rem 0;
}

.px-2 {
  padding: 0 2rem;
}

.mb-5 {
  margin-bottom: 5rem;
}

.w-25 {
  width: 25%;
}

.w-70 {
  width: 70%;
}

.w-5 {
  width: 5%;
}

.w-10 {
  width: 10%;
}

.centrar {
  display: block;
  margin: 0 auto;
}

.btn {
  padding: 1rem 10rem;
  border: none;
  background-color: #40C2D6;
  color: #FFFFFF;
  border-radius: 20px;
  margin: 5rem 0;
}

.btn-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}

.sinborde {
  border-bottom: 0;
}

header {
  box-shadow: 0 5px 10px -2px gray;
}

.nav__principal {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__principal i {
  font-size: 2rem;
  background-color: #40C2D6;
  color: #FFFFFF;
  margin-right: 1rem;
  padding: 1rem;
  border-radius: 50%;
}
.nav__principal i:hover {
  background-color: #04DBFC;
}

.tab {
  float: left;
  border: 1px solid #ccc;
  background-color: #FFFFFF;
  width: 20%;
  height: 300px;
}
.tab .tab__title {
  display: flex;
  justify-content: center;
}
.tab .tab__title .tab__text {
  text-transform: uppercase;
  font-weight: bold;
  padding: 2rem 0;
  color: #40C2D6;
}
.tab .tablinks {
  border-top: 1px solid #eceeef;
}
.tab .tablinks__box {
  width: 20px;
}
.tab button {
  display: block;
  background-color: inherit;
  color: #000000;
  padding: 22px 16px;
  width: 100%;
  border: none;
  outline: none;
  text-align: left;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
}
.tab button i {
  font-size: 2rem;
  padding: 0 5rem;
  color: #40C2D6;
}
.tab button:hover, .tab button:active {
  background-color: #40C2D6;
  color: #FFFFFF;
}

.tab button.active {
  background-color: #40C2D6;
  color: #FFFFFF;
}

.tabcontent {
  float: left;
  padding: 0px 12px;
  width: 60%;
  border-left: none;
  height: 300px;
}

.roles__grid {
  display: grid;
  grid-template-columns: 1fr;
}
.roles__grid .roles__head--flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.roles__grid .roles__head--flex:nth-child(1) {
  border-bottom: 3px solid #40C2D6;
}
.roles__grid .roles__head--flex .roles__title {
  color: #40C2D6;
  font-weight: bold;
  text-align: center;
}
.roles__grid .roles__head--flex .roles__text {
  text-align: center;
}
.roles__grid .roles__head--flex .roles__text--blue {
  background-color: #40C2D6;
  color: #FFFFFF;
  border-radius: 20px;
  padding: 1rem 0;
}
.roles__grid .roles__head--flex .roles__text--gray {
  background-color: #eceeef;
  color: #000000;
  border-radius: 20px;
  padding: 1rem 0;
}
.roles__grid .roles__head--flexsb:nth-child(1) {
  border-bottom: unset;
}
.roles__grid .line {
  border-bottom: 1px solid #eceeef;
}

.roles__grid--borderb {
  padding-bottom: 5rem;
  border-bottom: 1px solid #40C2D6;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #40C2D6;
}

input:focus + .slider {
  box-shadow: 0 0 1px #40C2D6;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* The container must be positioned relative: */
.cali__select {
  position: relative;
  font-family: Arial;
}

.cali__select select {
  display: none;
  /*hide original SELECT element: */
}

.select-selected {
  background-color: DodgerBlue;
}

/* Style the arrow inside the select element: */
.select-selected:after {
  position: absolute;
  content: "";
  top: 14px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: #fff transparent transparent transparent;
}

/* Point the arrow upwards when the select box is open (active): */
.select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 7px;
}

/* style the items (options), including the selected item: */
.select-items div, .select-selected {
  color: #ffffff;
  padding: 8px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
}

/* Style items (options): */
.select-items {
  position: absolute;
  background-color: DodgerBlue;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}

/* Hide the items when the select box is closed: */
.select-hide {
  display: none;
}

.select-items div:hover, .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}

.cali {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 2rem;
  border-bottom: 3px solid #40C2D6;
  gap: 2rem;
}

.cali__contain {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.cali__contain .cali__title {
  text-align: center;
  color: #40C2D6;
  font-weight: bold;
}

.flexbox p {
  color: #40C2D6;
  font-weight: bold;
  text-align: center;
}

.cali__ul .cali__ul--item {
  text-align: center;
  background-color: #40C2D6;
  color: #FFFFFF;
  padding: 0.5rem 6rem;
  border-radius: 15px;
}
.cali__ul .cali__ul--itemG {
  background-color: #eceeef;
  color: #000000;
}
.cali__ul .far {
  font-size: 2.5rem;
}

.default {
  color: #eceeef;
}

.blue {
  color: #40C2D6;
}

.periodos__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5rem 0;
  padding-bottom: 5rem;
  border-bottom: 3px solid #40C2D6;
}

#myBar {
  width: 75%;
  height: 25px;
  background-color: #40C2D6;
  text-align: center;
  /* To center it horizontally (if you want) */
  line-height: 25px;
  /* To center it vertically */
  color: #FFFFFF;
  border-radius: 15px;
}